import {settings} from '../../settings/config.js';

const yearOptions = [
  {
    content: '',
    value: 0,
    selected: true,
  }].concat([...Array(119).keys()].map(
    (num) => ({
      content: num + 1900,
      value: num + 1900,
    })
).reverse());

const monthOptions = [
  {content: '', value: 0, selected: true},
  {content: '', value: 1},
  {content: '', value: 2},
  {content: '', value: 3},
  {content: '', value: 4},
  {content: '', value: 5},
  {content: '', value: 6},
  {content: '', value: 7},
  {content: '', value: 8},
  {content: '', value: 9},
  {content: '', value: 10},
  {content: '', value: 11},
  {content: '', value: 12},
];

const dayOptions = [{
  content: '',
  value: 0,
  selected: true,
}].concat([...Array(30).keys()].map(
    (num) => ({
      content: num + 1,
      value: num + 1,
    })));

/**
 * Get template of the profile's update page for user
 * @param {Object} user
 * @return {Array} template of the profile's update page
 */
export default function template(user) {
  const date = [
    {
      block: 'select',
      fieldName: 'selectDay',
      attrs: {id: 'signup__selectDay'},
      options: dayOptions,
    },
    {
      block: 'select',
      fieldName: 'selectMonth',
      attrs: {id: 'signup__selectMonth'},
      options: monthOptions,
    },
    {
      block: 'select',
      fieldName: 'selectYear',
      attrs: {id: 'signup__selectYear'},
      options: yearOptions,
    },
  ];
  setSelectedDate(user, date);

  const gender = {
    block: 'select',
    fieldName: 'selectMale',
    options: [
      {content: '', value: ''},
      {content: '', value: 'male'},
      {content: '', value: 'female'},
      {content: '', value: 'other'},
    ],
  };
  setSelectedGender(user, gender);

  return [{
    block: 'profile-popup',
    mods: {main: true},
    content: [
      {
        block: 'title',
        wrappedInside: 'profile-popup',
        content: [' '],
      },
      {
        elem: 'content',
        content: [
          {
            elem: 'profile-icon',
            attrs: {
              src: user.img.startsWith(settings.imgPath) ?
                  user.img :
                  settings.imgPath + user.img,
            },
          },
          {
            block: 'form',
            attrs: {id: 'updateForm', novalidate: true},
            name: 'updateForm',
            mix: {'block': 'signup-form'},
            content: [
              {
                block: 'profile-info',
                wrappedInside: 'profile-popup',
                fields: [
                  {
                    name: '',
                    novalidate: true,
                    value: {
                      block: 'file-input',
                      fieldName: 'avatar',
                    },
                  },
                  {
                    name: '',
                    fieldName: 'firstName',
                    fieldAttrs: {
                      placeholder: '',
                      value: user.firstName,
                      checkable: true,
                      checkType: 'name',
                    },
                  },
                  {
                    name: '',
                    fieldName: 'lastName',
                    fieldAttrs: {
                      placeholder: '',
                      value: user.lastName,
                      checkable: true,
                      checkType: 'lastName',
                    },
                  },
                  {
                    name: 'Email',
                    fieldName: 'email',
                    fieldAttrs: {
                      checkable: true,
                      checkType: 'email',
                      required: true,
                      value: user.email,
                      type: 'email',
                      placeholder: 'your.name@site.com',
                    },
                  },
                  {
                    name: '',
                    fieldName: 'username',
                    fieldAttrs: {
                      required: true,
                      checkable: true,
                      value: user.username || user.nickname,
                      placeholder: 'username',
                      checkType: 'userName',
                    },
                  },
                  {
                    name: '',
                    novalidate: true,
                    value: gender,
                  },
                  {
                    name: ' ',
                    novalidate: true,
                    value: [
                      {
                        block: 'field-group',
                        content: date,
                      },
                    ],
                  },
                  {
                    name: ' ',
                    fieldName: 'password',
                    fieldAttrs: {
                      type: 'password',
                      placeholder: '',
                      checkable: true,
                      checkType: 'password',
                    },
                  },
                  {
                    name: ' ',
                    fieldName: 'passwordRepeat',
                    fieldAttrs: {
                      type: 'password',
                      placeholder: ' ',
                      checkable: true,
                      checkType: 'repeatPassword',
                    },
                  },
                ],
              },
              {
                block: 'profile-popup',
                elem: 'double-btn',
                elemMods: {center: true},
                content: [
                  {
                    block: 'btn',
                    attrs: {'type': 'submit'},
                    mods: {size: 'large'},
                    wrappedInside: 'profile-popup',
                    icon: 'useredit',
                    btnText: '',
                  },
                  {
                    block: 'btn',
                    mods: {size: 'large', color: 'muted'},
                    wrappedInside: 'profile-popup',
                    icon: 'delete',
                    btnText: '',
                  },
                ],
              },
            ],
          },
        ],
      },
    ],
  },
  ];
}

/**
 * Set selected gender
 * @param {Object} user
 * @param {Object} gender
 */
function setSelectedGender(user, gender) {
  if (user.gender !== '') {
    const options = gender.options;
    options.forEach((option) => {
      console.log(option, user.gender);
      if (option.value === user.gender) {
        option.selected = true;
      }
    });
  }
}

/**
 * Set selected date
 * @param {Object} user
 * @param {Array} date
 */
function setSelectedDate(user, date) {
  if (user.birthDate !== '' && user.birthDate !== null) {
    const userDate = user.birthDate.split('.');

    const dayOptions = date[0].options;
    dayOptions.forEach((option) => {
      if (option.content === +userDate[0]) {
        option.selected = true;
      }
    });

    const mounthOptions = date[1].options;
    mounthOptions.forEach((option) => {
      if (option.value === +userDate[1]) {
        option.selected = true;
      }
    });

    const yearOptions = date[2].options;
    yearOptions.forEach((option) => {
      if (option.content === +userDate[2]) {
        option.selected = true;
      }
    });
  }
}
